.svg-graphic {
  width: 100%;
  height: 0;
  padding-top: 77.7%;
  position: relative;
  background-color: #ffffff;
}
.svg-graphic svg {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #fff;
}
.svg-graphic svg .line-dots-clockwise {
  stroke-dasharray: 0.1, 15;
  -webkit-animation: dash 10s linear infinite;
          animation: dash 10s linear infinite;
  stroke-linecap: round;
}
.svg-graphic svg .line-dots-counterclockwise {
  stroke-dasharray: 0.1, 15;
  -webkit-animation: dash-left-to-right 10s linear infinite;
          animation: dash-left-to-right 10s linear infinite;
  stroke-linecap: round;
}
.svg-graphic svg .line1 {
  stroke-dasharray: 0.1, 15;
  -webkit-animation: dash 10s linear infinite;
          animation: dash 10s linear infinite;
  stroke-linecap: round;
}
.svg-graphic svg .line2 {
  stroke-dasharray: 0.1, 15;
  -webkit-animation: dash-left-to-right 10s linear infinite;
          animation: dash-left-to-right 10s linear infinite;
  stroke-linecap: round;
}
@-webkit-keyframes dash {
  from {
    stroke-dashoffset: 360;
  }
  to {
    stroke-dashoffset: 40;
  }
}
@keyframes dash {
  from {
    stroke-dashoffset: 360;
  }
  to {
    stroke-dashoffset: 40;
  }
}
@-webkit-keyframes dash-left-to-right {
  from {
    stroke-dashoffset: 40;
  }
  to {
    stroke-dashoffset: 360;
  }
}
@keyframes dash-left-to-right {
  from {
    stroke-dashoffset: 40;
  }
  to {
    stroke-dashoffset: 360;
  }
}



